<template>
 <el-upload
    class="upload-demo"
    drag
    action="/api/upload_hdf5file"
    multiple
    :before-upload="beforeFileUpload">
  >
    <i class="el-icon-upload"></i>
    <div class="el-upload__text">
       <em></em>
    </div>
    <template #tip>
      <div class="el-upload__tip">
        AGS AlphaHDF5
      </div>
    </template>
  </el-upload>
</template>

<script>
export default {
  name: "Hdf5Uploader",
  methods:{
    beforeFileUpload(file){
       // 
      var fileExtension = file.name.split('.').pop().toLowerCase();
      const ishdf5 = fileExtension === "hdf5";
      if (!ishdf5) {
        this.$message.error("HDF5");
      }
      return ishdf5;
    }
  }
}
</script>

