<template>
  <el-table :data="tableData">
    <el-table-column prop="name" label="" width="180"/>
    <el-table-column prop="date" label="" width="180"/>
  </el-table>
  <el-button type="primary" @click="getfilelist()"></el-button>
</template>

<script>
import axios from 'axios'
export default {
  name: "Hdf5FileSelector",
   data() {
    return {
      tableData: [],
    }
  },
  methods:{
    getfilelist(){
      const path = 'http://127.0.0.1:5000/api/get_hdf5_file_list'
      axios.get(path)
          .then(response => {
            this.tableData = response.data.fileList
          })
          .catch(error => {
            console.log(error)
          })
    }
  },

}
</script>

